body{
  overflow: scroll;
  --blue-1:    #004870;
  --blue-2:    #137cc1;
  --tan:       #e7e7e8;
  --something: #333333;
  --orange:    #fd7037;
  --off-white: #f7f7f7;
  color: var(--blue-1);
  background: var(--off-white);
}
.container{
  color: black;
}
#app-container{
}

#main-container{
  width: 100vw;
  min-height: 520px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
#header-wrap{
  position: fixed;
  width: 100vw;
  height: 56px;
  font-size: 25px;
  display: grid;
  grid-template-columns: 100px auto 100px;
  background: var(--header-footer-color);
  -webkit-box-shadow: 0 2px 3px rgba(0,0,0,0.3);
          box-shadow: 0 2px 3px rgba(0,0,0,0.3);
  z-index: 999;
}

#nav-bar-strip{
  height: 5px;
  width: 100vw;
  background: -webkit-gradient(linear, left top, right top, from(var(--blue-1)), to(var(--blue-2)));
  background: -webkit-linear-gradient(left, var(--blue-1), var(--blue-2));
  background: -o-linear-gradient(left, var(--blue-1), var(--blue-2));
  background: linear-gradient(to right, var(--blue-1), var(--blue-2));
}
#page-nav{
  background-color: white;
}
.nav-wrapper{
  padding: 0 10px 10px 10px;
}
i.material-icons{
  color: var(--blue-1);
  font-size: 35px !important;
}
a.brand-logo>a{
  color: var(--blue-1);
  font-size: 28px;
}
a.brand-logo:hover{
  text-decoration: none;
  color: var(--blue-2);
}
#nav-mobile > li > a {
  font-size: 20px;
  margin: 10px 0;
  color: var(--blue-1);
}
#nav-mobile.side-nav::before{
  content:'';
  width: 100%;
  height: 5px;
  position: absolute;
  background: -webkit-gradient(linear, left top, right top, from(var(--blue-1)), to(var(--blue-2)));
  background: -webkit-linear-gradient(left, var(--blue-1), var(--blue-2));
  background: -o-linear-gradient(left, var(--blue-1), var(--blue-2));
  background: linear-gradient(to right, var(--blue-1), var(--blue-2));
  font-weight: lighter;
}
#nav-mobile.side-nav{
  background: var(--off-white);
}
ul> li > a{
  font-size: 18px;
  color: var(--blue-1);
}
ul> li > a:hover{
  background: var(--off-white);
  color: var(--blue-2);
  text-decoration: none;
}
#guest-view{
}

#content-div{
  margin-bottom: 22px;
}
#log-buttons{
  display: grid;
  height: 56px;
  grid-template-columns: repeat(6,1fr);
  text-align: center;
}
.log-button{
  height: 50px;
  width: 150px;
  border-radius: 3px;
  text-align: center;
  padding: 15px;
  -webkit-transform: translateY(-25px);
      -ms-transform: translateY(-25px);
          transform: translateY(-25px);
  -webkit-box-shadow: 0 0 5px rgba(0,0,0,0.3);
          box-shadow: 0 0 5px rgba(0,0,0,0.3);
  -webkit-transition: all 200ms ease;
  -o-transition: all 200ms ease;
  transition: all 200ms ease;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.log-button:hover{
  cursor: pointer;
  -webkit-box-shadow: 0 0 5px rgba(0,0,0,0.7);
          box-shadow: 0 0 5px rgba(0,0,0,0.7);
}
#sign-up{
  grid-column: 2;
}
#log-in{
  grid-column: 5;
}
#content-container{
  position: relative;
  margin: 0 0 0 0;
}
#main-content-wrap{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.main-content{
  margin: 10px auto;
  padding: 10px;
  min-height: 400px;
  width: 95%;
  min-width: 320px;
  max-width: 840px;
  -webkit-box-shadow: 0 0 5px rgba(0,0,0,0.3);
          box-shadow: 0 0 5px rgba(0,0,0,0.3);
}

#carousel-container{
  height: 300px;
  background: #fff;
  -webkit-box-shadow: 0 0 2px rgba(0,0,0,0.6);
          box-shadow: 0 0 2px rgba(0,0,0,0.6);
  border-radius: 4px;
}
#carousel-container:hover{
  -webkit-box-shadow: 0 0 5px rgba(0,0,0,0.6);
          box-shadow: 0 0 5px rgba(0,0,0,0.6);
}
#main-carousel{
  height: 100%;
}
#category-carousel{
  max-width: 500px;
  min-width: 400px;
  margin: 0 auto;
  text-align: center;
}
#carousel-header{
  font-size: 25px;
  padding-top: 0;
}
.carousel-icon{
  margin-top: 40px;
  font-size: 90px;
}
.carousel-control.left, .carousel-control.right {
    background-image: none
}
.carousel-indicators{
  display: none;
}
#zip-input-form{
  color: #000;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-line-pack: center;
      align-content: center;
  width: 300px;
  margin: 0 auto;
}
#zip-wrapper{
  margin: 0 auto 15px auto;
}
#footer-wrap{
  padding: 10px;
  position: relative;
  margin: auto 0 0 0;
  height: 231px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  -webkit-box-shadow: 0 -2px 5px rgba(0,0,0,0.3);
          box-shadow: 0 -2px 5px rgba(0,0,0,0.3);
  background: var(--header-footer-color);
}

#social-links{
  margin-top: 10px;
  margin-left: 10px;
  display: grid;
  grid-template-columns: repeat(6,1fr);
}
#fb-link{
  grid-column: 1;

}
#tw-link{
  grid-column: 3;
}
#fb-link:hover{
  color: #3b5998;
  text-shadow: 0 0 5px rgba(0,0,0,0.3);
}
#tw-link:hover{
  color: #4099ff;
    text-shadow: 0 0 5px rgba(0,0,0,0.3);
}
.social-link{
  font-size: 30px;
  text-align: left;
  cursor: pointer;
  -webkit-transition: color 200ms ease;
  -o-transition: color 200ms ease;
  transition: color 200ms ease;
}
.social-link:hover{
}

#footer-side-info{
  grid-column: 2;
  display: grid;
  grid-template-rows: repeat(3, 1fr);
}
#footer-logo{
  text-align: center;
  padding: auto;
  grid-row: 1/3;
}
#footer-bottom-info{
  grid-row: 3;
}
#footer-email{

}
#footer-copyright{

}

.view-container{
  text-align: center;
  min-height: 550px;
  position: relative;
  overflow: scroll;
}

#register-view, #login-view{
  display: grid;
  grid-template-rows: 1fr 1fr;
}
#onsite{
  grid-row: 1/2;
  min-height: 512px;
  padding: 10px;
  border-right: 1px solid rgba(0,0,0,0.3);
}
#user-reg{
  padding: 10px;
}
.tab-content{
  padding-top:10px;
}

#offsite{
  grid-row: 2/3;
  border-left: 1px solid rgba(0,0,0,0.3);
}
#register-view > #onsite {
}
#register-view > #offsite {
}
#login-view > #onsite > .form-horizontal {
  position: relative;
  margin: auto;
}
#login-view > #offsite {
}
.fade-enter{
  opacity: 0;
}
.fade-enter-active{
  opacity: 1;
  -webkit-transition: opacity 300ms ease-in;
  -o-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in;
}
.fade-appear{
  opacity: 0;
}
.fade-appear-active{
  opacity: 1;
  -webkit-transition: opacity 300ms ease-in;
  -o-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in;
}

button.btn.btn-primary:hover{
  -webkit-transform: translateY(-1px);
      -ms-transform: translateY(-1px);
          transform: translateY(-1px);
  -webkit-box-shadow: 0 2px 2px rgba(0,0,0,0.6);
          box-shadow: 0 2px 2px rgba(0,0,0,0.6);
}

/*fullscreen*/
@media screen and (min-width: 750px) {
  #splash-container{
    position: absolute;
    z-index: -1;
  }
  #splash-container > .splash-img{
    width: 100vw;
    max-height: 800px;
  }

  #guest-view{
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  #guest-left{
    position: relative;
    grid-column: 1;
  }
  #guest-right{
    position: relative;
    grid-column: 2;
  }

  #register-view, #login-view{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: none;
  }
  #onsite{
    grid-row: none;
    grid-column: 1/2;
    min-height: 512px;
    border-right: 1px solid rgba(0,0,0,0.3);
  }
  #offsite{
    grid-row: none;
    grid-column: 2/3;
    border-left: 1px solid rgba(0,0,0,0.3);
  }
}



